const isLog = 1

const Config = {
	bmobDefaultAccount: {
		applicationId: "b7d2ba4c899fb0371e6ca184df301141",
		restApiKey: "a0af24c5cd6196b9457f0b56c420192a",
		masterKey: "7acfea947e08ce49572a66b72089194e",
		wechatApp: "wechatApp"
	},
	bmobUrl: 'https://api.bmobcloud.com/1',
	bmobAccountTableName: 'BmobAccount',
	bmobAccount: null,
	bmobAccountCallbacks: [],
	isBmobAccountLoading: false,
	functionName: 'request'
}


export default Request = (params, time = 0) => {
	//console.log(date, (new Date().valueOf() - date) / 1000 + 's', 'success', results)
	var date = time || new Date().valueOf()

	var isCloud = (params && params.headers && params.headers.Referer) || (!window.uni && params.url.indexOf(Config.bmobUrl) != 0)
	var method = isCloud ? "cloud" : window.uni ? 'uni' : 'axios'
	if (time == 0) {
		console.log(date, method, params)
	}
	return new Promise(function (resolve, reject) {
		if (isCloud) {
			if (Config.bmobAccount == null) {
				if (Config.isBmobAccountLoading == true) {
					Config.bmobAccountCallbacks.push({
						params,
						resolve,
						reject,
					});
				} else {
					Config.isBmobAccountLoading == true
					find(Config.bmobAccountTableName, {
						limit: '100',
						order: '-dueDate',
					}).then(results => {
						Config.isBmobAccountLoading = false
						if (results.length == 0 || results[0].dueDate.iso < formatTime(new Date())) {
							reject("BmobAccount全部过期")
						} else {
							Config.bmobAccount = results[0]
							Config.bmobAccountCallbacks.map(function (item) {
								cloudRun({ functionName: 'request', bmobAccount: Config.bmobAccount, params: item.params, resolve: item.resolve, reject: item.reject, date })
							})
							Config.bmobAccountCallbacks = []
							cloudRun({ functionName: 'request', bmobAccount: Config.bmobAccount, params: params, resolve: resolve, reject: reject, date })
						}
					}).catch(res => {
						Config.isBmobAccountLoading = false
					})
				}
			} else {
				cloudRun({ functionName: 'request', bmobAccount: Config.bmobAccount, params: params, resolve: resolve, reject: reject, date })
			}
		} else if (window.uni) {
			params.data = params.params
			params.header = params.headers
			params.params = undefined
			params.headers = undefined
			params.success = (res) => {
				resolve(res.data)
			}
			params.fail = (res) => {
				reject(res)
			}
			uni.request(params)
		} else {
			require('axios')(params).then(res => {
				resolve(res.data)
			}).catch(res => {
				reject(res)
			})
		}
	}).then(res => {
		if (!isCloud) {
			console.log(date, (new Date().valueOf() - date) / 1000 + 's', 'success', res)
		}
		return res
	}).catch(res => {
		if (!isCloud) {
			console.log(date, (new Date().valueOf() - date) / 1000 + 's', 'fiale', res)
		}
		return res
	})
};

function cloudRun({ functionName, bmobAccount, params, resolve, reject, date }) {
	return Request({
		method: 'POST',
		url: Config.bmobUrl + "/functions/" + functionName,
		data: params,
		headers: {
			'X-Bmob-Application-Id': bmobAccount.applicationId,
			'X-Bmob-Master-Key': bmobAccount.masterKey,
			'X-Bmob-REST-API-Key': bmobAccount.restApiKey,
			'X-Bmob-SDK-Type': 'wechatApp',
		},
	}, date).then(res => {
		var response = JSON.parse(res.result)
		response.body = eval('(' + response.body + ')')
		resolve(response.body)
	}).catch(res => {
		reject(res)
	})
}

export function find(tableName, params = {}) {
	return Request({
		url: Config.bmobUrl + "/classes/" + tableName,
		params,
		headers: {
			'X-Bmob-Application-Id': Config.bmobDefaultAccount.applicationId,
			'X-Bmob-Master-Key': Config.bmobDefaultAccount.masterKey,
			'X-Bmob-REST-API-Key': Config.bmobDefaultAccount.restApiKey,
			'X-Bmob-SDK-Type': 'wechatApp',
		}
	}).then(res => {
		return res.results
	}).catch(res => {
		return res
	})
}

const console = {
	log: function () {
		if (isLog && location.host.indexOf('192.168') > -1) window.console.log(...arguments)
	}
}

function formatTime(date) {
	var year = date.getFullYear()
	var month = date.getMonth() + 1
	var day = date.getDate()
	var hour = date.getHours()
	var minute = date.getMinutes()
	var second = date.getSeconds()
	return [year, month, day].map(formatNumber).join('-') + ' ' + [hour, minute, second].map(
		formatNumber).join(':')
}

function formatNumber(n) {
	n = n.toString()
	return n[1] ? n : '0' + n
}

function removeAllSpace(str) {
	return str.replace(/\s+/g, "");
}
