export function getQueryString(name) {
  var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
  var r = window.location.search.substr(1).match(reg);
  if (r != null) return unescape(r[2]);
  return null;
}

export function getQueryParams() {
  var url = location.search; //获取url中"?"符后的字串 
  var theRequest = new Object();
  if (url.indexOf("?") != -1) {
    var str = url.substr(1);
    var strs = str.split("&");
    for (var i = 0; i < strs.length; i++) {
      theRequest[strs[i].split("=")[0]] = unescape(strs[i].split("=")[1]);
    }
  }
  return theRequest;
}

export function formatUrl(params) {
  var origin = location.origin
  var search = location.search
  var hash = location.hash
  var url = origin

  params = Object.assign(getQueryParams(), params)
  var i = 1
  for (const key in params) {
    url += (i == 1 ? '?' : '&') + key + '=' + params[key]
    i++
  }
  url += hash
  return url;
}

export const deviceInfo = {

  isPC() {
    var userAgentInfo = navigator.userAgent;
    var Agents = ["Android", "iPhone",
      "SymbianOS", "Windows Phone",
      "iPad", "iPod"
    ];
    var flag = true;
    for (var v = 0; v < Agents.length; v++) {
      if (userAgentInfo.indexOf(Agents[v]) > 0) {
        flag = false;
        break;
      }
    }
    return flag;
  },
  isMobile() {
    return !this.isPC()
  },
  isIOS() {
    return this.isMobile() && navigator.userAgent.indexOf('iPhone') > -1
  },
  isAndroid() {
    return this.isMobile() && navigator.userAgent.indexOf('Android') > -1
  },
  isWeiXin() {
    return navigator.userAgent.indexOf('micromessenger') > -1
  },
  isSafari() {
    return navigator.userAgent.indexOf('Safari') > -1 && navigator.vendor.indexOf('Apple') > -1
  }
}



export function randomColor(index) {
  let r, g, b;
  r = Math.floor(Math.random() * 256);
  g = Math.floor(Math.random() * 256);
  b = Math.floor(Math.random() * 256);
  return "rgb(" + r + ',' + g + ',' + b + ")";
}
